.cards__value {
  color: white;
  text-align: right; }

.cards__link {
  color: #ef7f1a;
  text-decoration: underline;
  font-weight: 700; }

.cards__description {
  color: black;
  padding: 10px 10px 30px;
  transition: 0.5s all; }

.cards__title {
  font-family: "Yanone Kaffeesatz", sans-serif;
  font-size: 24px;
  line-height: 24px;
  margin: 0; }

.cards__text {
  font-weight: 300;
  font-size: 16px;
  line-height: 20px;
  max-width: 370px;
  width: 90%;
  margin: 8px 0; }

.cards__inner {
  display: flex; }

.cards__size {
  font-size: 16px;
  font-weight: 700;
  color: #bababa;
  border: 1px solid #bababa;
  padding: 0 12px;
  border-radius: 3px;
  transition: 0.3s all;
  cursor: pointer; }

.cards__checkbox-input {
  position: absolute;
  z-index: -1;
  opacity: 0; }
  .cards__checkbox-input:focus + .cards__size {
    border: 1px solid black; }
  .cards__checkbox-input:checked + .cards__size {
    color: #ef7f1a;
    border: 1px solid #ef7f1a; }

.cards__size-text {
  font-family: "Yanone Kaffeesatz", sans-serif;
  font-size: 18px;
  text-transform: uppercase;
  margin: 10px 0; }

.cards__prices {
  margin-left: auto; }
  @media (min-width: 768px) {
    .cards__prices {
      margin-right: 30px; } }

.cards__price {
  font-family: "Yanone Kaffeesatz", sans-serif;
  font-weight: 700;
  font-size: 36px; }
  .cards__price--discount {
    font-weight: 400;
    font-size: 24px;
    color: #bababa;
    text-decoration: line-through; }

.cards__icon {
  fill: #ef7f1a; }
  .cards__icon:hover, .cards__icon:focus {
    opacity: 0.7; }
